// External Imports
import { useSelector, useDispatch } from 'react-redux';

// Internal Imports
import { onInfoChange, onEduSubmit } from './jsComponents/actions';
import patterns from './jsComponents/patterns';

// SASS
import './sass/education.scss';
import './sass/inputs.scss';

const Education = () => {
  const educationState = useSelector((state) => state.education);
  const { schoolName, major, educationStart, educationEnd } = educationState;

  const dispatch = useDispatch();

  const handleChange = (event) => {
    dispatch(onInfoChange(event.target.value, event.target.id));
  };

  return (
    <section className='education'>
      <h2>Education</h2>
      <form onSubmit={(event) => dispatch(onEduSubmit(event))}>
        <div>
          <h3>School Name:</h3>
          <input
            id='schoolName'
            type='text'
            value={schoolName}
            onChange={handleChange}
            pattern={patterns[schoolName]}
            title='Only alphabets are allowed'
          />
        </div>
        <div>
          <h3>Major:</h3>
          <input
            id='major'
            type='text'
            value={major}
            onChange={handleChange}
            pattern={patterns[major]}
            title='Only alphabets are allowed'
          />
        </div>
        <div>
          <h3>Start:</h3>
          <input
            id='educationStart'
            type='text'
            value={educationStart}
            onChange={handleChange}
            placeholder='MM/YYYY'
            pattern={patterns[educationStart]}
            title='MM/YYYY'
          />
        </div>
        <div>
          <h3>Graduation:</h3>
          <input
            id='educationEnd'
            type='text'
            value={educationEnd}
            onChange={handleChange}
            placeholder='MM/YYYY'
            pattern={patterns[educationEnd]}
            title='MM/YYYY'
          />
        </div>
        <button type='submit'>Add Education</button>
      </form>
    </section>
  );
};

export default Education;
