// External Imports
import { useDispatch, useSelector } from 'react-redux';

// Internal Imports
import { onInfoChange } from './jsComponents/actions';
import patterns from './jsComponents/patterns';

// SASS
import './sass/inputs.scss';

const GeneralInfoInputs = (props) => {
  const { name, id, title } = props;
  const generalInfo = useSelector((state) => state.generalInfo);
  const dispatch = useDispatch();

  const handleChange = (event) => {
    dispatch(onInfoChange(event.target.value, event.target.id));
  };

  return (
    <div className='general-input'>
      <h3>{name}:</h3>
      <input
        id={id}
        value={generalInfo[id]}
        onChange={handleChange}
        pattern={patterns[id]}
        title={title}
      />
    </div>
  );
};

export default GeneralInfoInputs;
