import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { onDeleteEdu, onDeleteExp, onDeleteProj } from './jsComponents/actions';

// SASS
import {
  faPhoneSquare,
  faAt,
  faTrash,
} from '@fortawesome/free-solid-svg-icons';
import { faGithubSquare, faLinkedin } from '@fortawesome/free-brands-svg-icons';
import './sass/preview.scss';

const Preview = React.forwardRef((props, ref) => {
  const state = useSelector((state) => state);

  const dispatch = useDispatch();

  const currentMonth = `0${new Date().getMonth() + 1}`.slice(-2);
  const currentYear = `${new Date().getFullYear()}`;

  const info = state.generalInfoData.map((data) => {
    return (
      <>
        <h1>{data.name}</h1>
        <div className='contacts'>
          <a href={data.email} target='_blank' rel='noreferrer noopener'>
            {data.email}
            <FontAwesomeIcon icon={faAt} className='fa-icon' />
          </a>
          <div>
            {data.telephone}
            <FontAwesomeIcon icon={faPhoneSquare} className='fa-icon' />
          </div>
          <a href={data.linkedin} target='_blank' rel='noreferrer noopener'>
            {data.linkedin}
            <FontAwesomeIcon icon={faLinkedin} className='fa-icon' />
          </a>
          <a href={data.github} target='_blank' rel='noreferrer noopener'>
            {data.github}
            <FontAwesomeIcon icon={faGithubSquare} className='fa-icon' />
          </a>
        </div>
      </>
    );
  });

  const education = state.educationData.map((data) => {
    return (
      <div className='details' id={data.id}>
        <div className='information'>
          <h2>{data.schoolName}</h2>
          <p>{data.major}</p>
        </div>
        <div className='years'>
          {data.educationStart} -{' '}
          {data.educationEnd === currentMonth + '/' + currentYear
            ? 'Present'
            : data.educationEnd}
          <div
            onClick={() => {
              dispatch(onDeleteEdu('eduDelete', data.id));
            }}
          >
            <FontAwesomeIcon className='trash-icon' icon={faTrash} size='2x' />
          </div>
        </div>
      </div>
    );
  });

  const experience = state.experienceData.map((data) => {
    return (
      <div className='details' id={data.id}>
        <div className='information'>
          <h2>{data.companyName}</h2>
          <p>{data.position}</p>
          <div
            id='expDescr-preview'
            dangerouslySetInnerHTML={{ __html: data.expDescr }}
          />
        </div>
        <div className='years'>
          {data.from} -{' '}
          {data.to === currentMonth + '/' + currentYear ? 'Present' : data.to}
          <div
            onClick={() => {
              dispatch(onDeleteExp('expDelete', data.id));
            }}
          >
            <FontAwesomeIcon className='trash-icon' icon={faTrash} size='2x' />
          </div>
        </div>
      </div>
    );
  });

  const projects = state.projectsData.map((data) => {
    return (
      <div className='details' id={data.id}>
        <div className='information'>
          <h2>{data.projectName}</h2>
          <p>{data.institution}</p>
          <div
            id='projectDescr-preview'
            dangerouslySetInnerHTML={{ __html: data.projectDescr }}
          />
        </div>
        <div className='years'>
          {data.projectStart} -{' '}
          {data.projectEnd === currentMonth + '/' + currentYear
            ? 'Present'
            : data.projectEnd}
          <div
            onClick={() => {
              dispatch(onDeleteProj('projDelete', data.id));
            }}
          >
            <FontAwesomeIcon className='trash-icon' icon={faTrash} size='2x' />
          </div>
        </div>
      </div>
    );
  });

  return (
    <div className='preview' ref={ref}>
      {!!state.generalInfoData.length && <header>{info}</header>}
      {!!state.educationData.length && (
        <section className='section'>
          <h2>Education</h2>
          {education}
        </section>
      )}
      {!!state.experienceData.length && (
        <section className='section'>
          <h2>Experience</h2>
          {experience}
        </section>
      )}
      {!!state.projectsData.length && (
        <section className='section'>
          <h2>Projects</h2>
          {projects}
        </section>
      )}
    </div>
  );
});

export default Preview;
