// External
import { useDispatch } from 'react-redux';

// Local Imports
import GeneralInfoInputs from './GeneralInfoInputs';
import { onInfoSubmit } from './jsComponents/actions';

// SASS
import './sass/GeneralInfo.scss';

const GeneralInfo = () => {
  const dispatch = useDispatch();

  return (
    <form
      className='general-info'
      onSubmit={(event) => dispatch(onInfoSubmit(event))}
    >
      <GeneralInfoInputs
        name='Name'
        id='name'
        title='Only alphabets are allowed'
      />
      <GeneralInfoInputs
        name='Email'
        id='email'
        title='<something>@<something>.com'
      />
      <GeneralInfoInputs
        name='Phone Number'
        id='telephone'
        title='+00-000-00000000'
      />
      <GeneralInfoInputs
        name='LinkedIn'
        id='linkedin'
        title='linkedin.com/<userid>'
      />
      <GeneralInfoInputs
        name='Github'
        id='github'
        title='github.com/<username>'
      />
      <button type='submit'>Add Information</button>
    </form>
  );
};

export default GeneralInfo;
